body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  max-width: 1060px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toggle{
  border: none; 
  border-radius: 5px;
}

.dark-mode{
  background-color: rgb(50,50,50);
}
.dark-mode h1{
  color:azure;
}

.search{
  display: flex;
  align-items: center;
  background-color: lightgrey;
  padding: 5px;
  margin-bottom: 15px;
  margin-top: 5px;
  border-radius: 10px;
}

.input{
  width: 100%;
  border: none;
  background-color: transparent;
  outline: none;
}

.note-list{
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.note.new{
  background-color: #FFF9CA;
}

.add-icon,.delete-icon:hover{
  cursor:pointer;
}

textarea{
  border:none;
  resize: none;
  background-color: transparent;
  outline: none;
}

.note {
  background-color: pink;
  border-radius: 10px;
  padding: 20px;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.note-footer{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.colors{
  /* position: sticky;
  bottom: 5px; */
  display: flex;
  max-height: 100px;
  padding: 15px;
  justify-content: center;
}

.coloryellow,.colorpink,.colorpurple,.colororange{
  width: 7vh;
  height: 7vh;
  margin-right: 7px;
  margin-left: 7px;
  border: 2px solid lightgray;
  border-radius: 50%;
  cursor: pointer;
}
.coloryellow{
  background-color: #FFF9CA;
}
.colorpink{
  background-color: #FFB4B4;
}
.colorpurple{
  background-color: #B2A4FF;
}
.colororange{
  background-color: #FFDEB4;
}